import { useTranslation } from "react-i18next";
import { assestsUserAdmissionUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import GoBack from "../../Shared/StaffStudentProfile/GoBack";
import InstituteSideBg from "../Config/InstituteSideBg";
import InstituteSideCard from "../Config/InstituteSideCard";
import { Suspense, lazy, memo, useState } from "react";
import QvipleLoader from "../../Loader/QvipleLoader";
const UpdateInstituteAdmissionManager = lazy(() =>
  import("./UpdateInstituteAdmissionManager")
);

const InstituteAdmissionManagerSide = ({
  admissionHead,
  cover,
  ongoingCount,
  completedCount,
}) => {
  const { t } = useTranslation();
  const [openMenu, setOpenMenu] = useState(false);

  const onOpenMenu = () => {
    setOpenMenu((pre) => !pre);
  };

  // console.info("how many tmes");
  return (
    <>
      <div>
        <GoBack
          customStyle={{
            width: "100%",
            marginTop: "0",
          }}
        />
        <InstituteSideBg
          avatar={
            cover
              ? `${imageShowUrl}/${cover}`
              : `${assestsUserAdmissionUrl}/cover.svg`
          }
          onAction={onOpenMenu}
        />
        <InstituteSideCard
          title={`${admissionHead?.staffFirstName || ""} ${
            admissionHead?.staffMiddleName ? admissionHead?.staffMiddleName : ""
          } ${admissionHead?.staffLastName || ""}`}
          subtitle={t("admission_admin")}
          customStyleContainer={{
            borderRadius: "18px 18px 0px 0px",
            border: "none",
            marginTop: "-1.49vw",
          }}
        />
        <InstituteSideCard title={t("ongoing_add")} subtitle={t("admissions")}>
          <h6>{ongoingCount ?? 0}</h6>
        </InstituteSideCard>

        <InstituteSideCard
          title={t("application")}
          subtitle={t("received_from_students")}
          customStyleContainer={{
            borderRadius: "0 0 12px 12px",
            borderBottom: "0.4px solid rgba(0, 0, 0, 0.15)",
          }}
        >
          <h6>{onAddCount(ongoingCount, completedCount)}</h6>
        </InstituteSideCard>
      </div>
      {openMenu && (
        <Suspense fallback={<QvipleLoader />}>
          <UpdateInstituteAdmissionManager
            admissionHead={admissionHead}
            onClose={onOpenMenu}
          />
        </Suspense>
      )}
    </>
  );
};

export default memo(InstituteAdmissionManagerSide);

const onAddCount = (arg, arg2) => {
  if (arg && arg2) {
    return arg + arg2;
  } else {
    return 0;
  }
};
