import { Suspense, lazy } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import InstituteNavbarConfig from "../Config/InstituteNavbarConfig";
import InstituteMemberConfig from "../Config/InstituteMemberConfig";
import Loading from "../../Loader/Loading";
import QvipleLoader from "../../Loader/QvipleLoader";
import { useAdmissionDashboardMaster } from "../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import InstituteAdmissionManagerSide from "./InstituteAdmissionManagerSide";
import InstituteAdmissionManagerProfile from "./InstituteAdmissionManagerProfile";

const InstituteAdmissionTabController = lazy(() =>
  import("./InstituteAdmissionTabController")
);

const RequiredDocument = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/RequiredDocument/RequiredDocument"
  )
);
const AdmissionExport = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/AdmissionExport/AdmissionExport"
  )
);
const AdmissionModerator = lazy(() =>
  import("../../UserStaffMember/AdmissionManager/Moderator/AdmissionModerator")
);

const OngoingAdmission = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/OngoingAdmission/OngoingAdmission"
  )
);
const OneOngoingAdmissionConfig = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/OneOngoingAdmission/OneOngoingAdmissionConfig"
  )
);

const CompleteApplication = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/CompleteApplication/CompleteApplication"
  )
);

// const StudentStatitics = lazy(() =>
//   import(
//     "../../UserStaffMember/FinanceManager/StudentStatitics/StudentStatitics"
//   )
// );
const StudentStatiticsDetail = lazy(() =>
  import(
    "../../UserStaffMember/FinanceManager/StudentStatitics/StudentStatiticsDetail"
  )
);
const AdmissionCombinedApplicationConfig = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/AdmissionCombinedApplication/AdmissionCombinedApplicationConfig"
  )
);
const AdmissionCancelled = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/AdmissionCancelled/AdmissionCancelled"
  )
);
const AdmissionEnquiry = lazy(() =>
  import(
    "../../../MembersTab/staff/Mainbody/StaffAdmisionAdmin/AdmissionEnquiry/AdmissionEnquiry"
  )
);
const AdmissionOngoingApplication = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/AdmissionOngoingApplication/AdmissionOngoingApplication"
  )
);
const AdmissionReadmissionConfig = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/AdmissionReadmission/AdmissionReadmissionConfig"
  )
);
const AdmissionStudentFormSetting = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/FormSetting/AdmissionStudentFormSetting"
  )
);
const AdmissionSubjectCriteria = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/AdmissionSubjectCriteria/AdmissionSubjectCriteria"
  )
);
const AdmissionSubjectCriteriaDetail = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/AdmissionSubjectCriteria/AdmissionSubjectCriteriaDetail/AdmissionSubjectCriteriaDetail"
  )
);
const AdmissionSubjectCriteriaDetailNested = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/AdmissionSubjectCriteria/AdmissionSubjectCriteriaDetail/AdmissionSubjectCriteriaDetailNested/AdmissionSubjectCriteriaDetailNested"
  )
);
const AdmissionQr = lazy(() =>
  import("../../UserStaffMember/AdmissionManager/AdmissionQr/AdmissionQr")
);
const ApplicationSubjectListDetailConfig = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/OneOngoingAdmission/ApplicationSubjectList/ApplicationSubjectListDetailConfig"
  )
);
const AdmissionStatistics = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/AdmissionStatistics/AdmissionStatistics"
  )
);
const HostelOngoingAdmission = lazy(() =>
  import(
    "../../UserStaffMember/HostelManager/HostelOngoingAdmission/HostelOngoingAdmission"
  )
);
const OneHostelOngoingAdmissionConfig = lazy(() =>
  import(
    "../../UserStaffMember/HostelManager/OneHostelOngoingAdmission/OneHostelOngoingAdmissionConfig"
  )
);
const AdmissionSetting = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/AdmissionSetting/AdmissionSetting"
  )
);
const HostelExport = lazy(() =>
  import("../../UserStaffMember/HostelManager/HostelExport/HostelExport")
);
const RequiredDocumentDetail = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/RequiredDocument/RequiredDocumentDetail"
  )
);
const DocumentReminderMessageHistory = lazy(() =>
  import(
    "../../UserStaffMember/AdmissionManager/RequiredDocument/DocumentReminderMessageHistory"
  )
);
const InstituteAdmissionManager = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { admissionDashboardMaster, admissionDashboardMasterLoading } =
    useAdmissionDashboardMaster({
      data: {
        aid: getQuery.state?.aid,
        mid: "",
      },
      skip: !getQuery.state?.aid,
    });

  // console.info("admissionDashboardMaster", admissionDashboardMaster);
  return (
    <InstituteNavbarConfig>
      <InstituteMemberConfig>
        <InstituteAdmissionManagerSide
          admissionHead={admissionDashboardMaster?.admissionAdminHead}
          cover={admissionDashboardMaster?.cover}
          ongoingCount={admissionDashboardMaster?.newAppCount}
          completedCount={admissionDashboardMaster?.completedCount}
        />
        <div>
          <Suspense fallback={<QvipleLoader />}>
            {!searchParams.get("type") ||
            ["fund"]?.includes(searchParams.get("type")) ? (
              <InstituteAdmissionManagerProfile
                aid={getQuery.state?.aid}
                admissionData={admissionDashboardMaster}
                tabManage={admissionDashboardMaster?.tab_manage}
              />
            ) : searchParams.get("type") === "document" ? (
              <RequiredDocument aid={admissionDashboardMaster?._id} />
            ) : searchParams.get("type") === "export" ? (
              <AdmissionExport
                aid={admissionDashboardMaster?._id}
                instituteId={admissionDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "moderator" ? (
              <AdmissionModerator
                aid={admissionDashboardMaster?._id}
                instituteId={admissionDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "statistics-detail" ? (
              <StudentStatiticsDetail />
            ) : searchParams.get("type") === "selected-subject" ? (
              <ApplicationSubjectListDetailConfig />
            ) : searchParams.get("type") === "ongoing" ? (
              <OngoingAdmission
                aid={admissionDashboardMaster?._id}
                instituteId={admissionDashboardMaster?.institute?._id}
                fid={admissionDashboardMaster?.institute?.financeDepart?.[0]}
              />
            ) : searchParams.get("type") === "ongoing-detail" ? (
              <OneOngoingAdmissionConfig
                instituteId={admissionDashboardMaster?.institute?._id}
                financeId={
                  admissionDashboardMaster?.institute?.financeDepart?.[0]
                }
                aid={admissionDashboardMaster?._id}
              />
            ) : searchParams.get("type") === "combine-application" ? (
              <AdmissionCombinedApplicationConfig
                instituteId={admissionDashboardMaster?.institute?._id}
                aid={admissionDashboardMaster?._id}
                financeId={
                  admissionDashboardMaster?.institute?.financeDepart?.[0]
                }
              />
            ) : searchParams.get("type") === "cancel-admission" ? (
              <AdmissionCancelled
                studentAlias={
                  admissionDashboardMaster?.institute?.alias_pronounciation
                    ?.student_alias
                }
                instituteId={admissionDashboardMaster?.institute?._id}
                aid={admissionDashboardMaster?._id}
              />
            ) : searchParams.get("type") === "complete" ? (
              <CompleteApplication
                aid={admissionDashboardMaster?._id}
                instituteId={admissionDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "enquiries" ? (
              <AdmissionEnquiry
                admissionId={admissionDashboardMaster?._id}
                instituteId={admissionDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "enq-application" ? (
              <AdmissionOngoingApplication
                aid={admissionDashboardMaster?._id}
                instituteId={admissionDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "internal" ? (
              <AdmissionReadmissionConfig
                aid={admissionDashboardMaster?._id}
                instituteId={admissionDashboardMaster?.institute?._id}
                studentAlias={
                  admissionDashboardMaster?.institute?.alias_pronounciation
                    ?.student_alias
                }
              />
            ) : searchParams.get("type") === "application-form" ? (
              <AdmissionStudentFormSetting />
            ) : searchParams.get("type") === "criteria" ? (
              <AdmissionSubjectCriteria aid={getQuery.state?.aid} />
            ) : searchParams.get("type") === "criteria-detail" ? (
              <AdmissionSubjectCriteriaDetail aid={getQuery.state?.aid} />
            ) : searchParams.get("type") === "criteria-subdetail" ? (
              <AdmissionSubjectCriteriaDetailNested
                aid={getQuery.state?.aid}
                instituteId={admissionDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "qr" ? (
              <AdmissionQr
                aid={getQuery.state?.aid}
                instituteId={admissionDashboardMaster?.institute?._id}
                qrImage={admissionDashboardMaster?.app_qr_code}
              />
            ) : searchParams.get("type") === "tab" ? (
              <InstituteAdmissionTabController
                admissionId={getQuery.state?.aid}
              />
            ) : searchParams.get("type") === "statistics" ? (
              <AdmissionStatistics
                financeId={
                  admissionDashboardMaster?.institute?.financeDepart?.[0]
                }
                aid={admissionDashboardMaster?._id}
                instituteId={admissionDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "hostel-ongoing" ? (
              <HostelOngoingAdmission
                hid={admissionDashboardMaster?.institute?.hostelDepart?.[0]}
                aid={getQuery.state?.aid}
                instituteId={admissionDashboardMaster?.institute?._id}
                fid={admissionDashboardMaster?.institute?.financeDepart?.[0]}
              />
            ) : searchParams.get("type") === "hostel-ongoing-detail" ? (
              <OneHostelOngoingAdmissionConfig
                hid={admissionDashboardMaster?.institute?.hostelDepart?.[0]}
                aid={getQuery.state?.aid}
                instituteId={admissionDashboardMaster?.institute?._id}
                financeId={
                  admissionDashboardMaster?.institute?.financeDepart?.[0]
                }
              />
            ) : searchParams.get("type") === "setting" ? (
              <AdmissionSetting
                aid={getQuery.state?.aid}
                masterData={admissionDashboardMaster}
              />
            ) : searchParams.get("type") === "hostel-export" ? (
              <HostelExport
                hid={admissionDashboardMaster?.institute?.hostelDepart?.[0]}
                instituteId={admissionDashboardMaster?.institute?._id}
              />
            ) : searchParams.get("type") === "document-detail" ? (
              <RequiredDocumentDetail aid={admissionDashboardMaster?._id} />
            ) : searchParams.get("type") === "reminder-message" ? (
              <DocumentReminderMessageHistory
                instituteId={admissionDashboardMaster?.institute?._id}
              />
            ) : null}
          </Suspense>
          {admissionDashboardMasterLoading && <Loading />}
        </div>
      </InstituteMemberConfig>
    </InstituteNavbarConfig>
  );
};

export default InstituteAdmissionManager;
