import { useLocation, useParams, useSearchParams } from "react-router-dom";
import RectangleTabCard from "../../Ui/Tab/Rectangle/RectangleTabCard";
import RectangleTabWrapper from "../../Ui/Tab/Rectangle/RectangleTabWrapper";
import UserMemberTabsWrapper from "../../Ui/UserMember/UserMemberTabsWrapper";
import SquareTabWrapper from "../../Ui/Tab/Square/SquareTabWrapper";
import SquareTabCard from "../../Ui/Tab/Square/SquareTabCard";
import {
  assestsFeedUrl,
  assestsUserAdmissionUrl,
  assestsUserFinanceUrl,
  assestsUtilityUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import { memo } from "react";
import AdmissionFund from "../../UserStaffMember/AdmissionManager/AdmissionFund/AdmissionFund";

const InstituteAdmissionManagerProfile = ({
  aid,
  admissionData,
  tabManage,
}) => {
  const params = useParams();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  let measure = document.getElementById("562xnbhgh")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    let disvible =
      measure > 820 ? (measure > 1100 ? 6 : 5) : measure > 700 ? 4 : 3;
    let mi = measure > 820 ? 0 : 2;
    width = `${Math.floor((measure - mi - 31 * disvible - 1) / disvible)}px`;
    height = width;
  }
  return (
    <>
      <UserMemberTabsWrapper>
        <RectangleTabWrapper>
          {tabList.map((tab) => (
            <RectangleTabCard
              key={tab?.id}
              label={tab.label}
              isLink
              isActive={
                tab.id === "menu" && !searchParams.get("type")
                  ? true
                  : searchParams.get("type") === tab.id
                  ? true
                  : false
              }
              parentState={getQuery.state}
              url={`/q/${params.username}/institute/member/admission${tab.urlKey}`}
              isAuthorized
            />
          ))}
        </RectangleTabWrapper>
        {!searchParams.get("type") ? (
          <SquareTabWrapper dynamicId="562xnbhgh">
            <SquareTabCard
              label="ongoing_admissions"
              iconUrl={`${assestsUserAdmissionUrl}/ongoing.svg`}
              url={`/q/${params.username}/institute/member/admission?type=ongoing`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              authorizedKey="ongoing_admission"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="combined_application"
              iconUrl={`${assestsUserAdmissionUrl}/ongoing.svg`}
              url={`/q/${params.username}/institute/member/admission?type=combine-application`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              authorizedKey="ongoing_admission"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="re_admission_only"
              iconUrl={`${assestsUserAdmissionUrl}/enquiries.svg`}
              url={`/q/${params.username}/institute/member/admission?type=internal`}
              state={getQuery.state}
              isAuthorized
              isVisible={{
                inertnal_admission: true,
              }}
              authorizedKey="inertnal_admission"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />

            <SquareTabCard
              label="complete_admission"
              iconUrl={`${assestsUserAdmissionUrl}/complete.svg`}
              url={`/q/${params.username}/institute/member/admission?type=complete`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              authorizedKey="complete_admission"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="cancel_admission"
              iconUrl={`${assestsUserAdmissionUrl}/cancelled.svg`}
              url={`/q/${params.username}/institute/member/admission?type=cancel-admission`}
              state={getQuery.state}
              isAuthorized
              isVisible={{ student_statitics: true }}
              authorizedKey="student_statitics"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="required_documents"
              iconUrl={`${assestsUserAdmissionUrl}/document.svg`}
              url={`/q/${params.username}/institute/member/admission?type=document`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              authorizedKey="required_documents"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="data_export"
              iconUrl={`${assestsUserFinanceUrl}/export.svg`}
              url={`/q/${params.username}/institute/member/admission?type=export`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              authorizedKey="data_export"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="admission_moderators"
              iconUrl={`${assestsUserAdmissionUrl}/moderator.svg`}
              url={`/q/${params.username}/institute/member/admission?type=moderator`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              authorizedKey="admission_mods"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />

            <SquareTabCard
              label="admission_enquiries"
              iconUrl={`${assestsUserAdmissionUrl}/enquiries.svg`}
              url={`/q/${params.username}/institute/member/admission?type=enq-application`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              authorizedKey="admission_enquiry"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="admission_statitics"
              iconUrl={`${assestsUserFinanceUrl}/statitics.svg`}
              url={`/q/${params.username}/institute/member/admission?type=statistics`}
              state={getQuery.state}
              isAuthorized
              isVisible={{ student_statitics: true }}
              authorizedKey="student_statitics"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />

            <SquareTabCard
              label="subject_criteria"
              iconUrl={`${assestsUserAdmissionUrl}/enquiries.svg`}
              url={`/q/${params.username}/institute/member/admission?type=criteria`}
              state={getQuery.state}
              isAuthorized
              isVisible={{ site_info: true }}
              authorizedKey="site_info"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="qr_code"
              iconUrl={`${assestsUtilityUrl}/qr-code.svg`}
              url={`/q/${params.username}/institute/member/admission?type=qr`}
              state={getQuery.state}
              isAuthorized
              isVisible={{ site_info: true }}
              authorizedKey="site_info"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="setting"
              iconUrl={`${assestsFeedUrl}/setting.svg`}
              url={`/q/${params.username}/institute/member/admission?type=setting`}
              state={getQuery.state}
              isAuthorized
              isVisible={{ site_info: true }}
              authorizedKey="site_info"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="tab_controller"
              iconUrl={`${assestsUserFinanceUrl}/controller.svg`}
              url={`/q/${params.username}/institute/member/admission?type=tab`}
              state={getQuery.state}
              isAuthorized
              isVisible={{ tab_controller: true }}
              authorizedKey="tab_controller"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
          </SquareTabWrapper>
        ) : (
          <AdmissionFund admissionData={admissionData} aid={aid} />
        )}
      </UserMemberTabsWrapper>
    </>
  );
};

export default memo(InstituteAdmissionManagerProfile);

const tabList = [
  {
    label: "menu",
    id: "menu",
    urlKey: "",
  },
  {
    label: "fund",
    id: "fund",
    urlKey: "?type=fund",
  },
];
